.btn{
    position: relative;
    display: flex;
    justify-content: center;
    padding: 0 5px;
    margin-right: 5px;
    border-radius: 5px;
    font-size: 0.9rem;
    background-color: rgb(222, 228, 240);
}
.title.active{
    color: white;
}

.title{
    z-index: 2;
    position: relative;
    color: rgb(46, 46, 49);
    text-decoration: none;
    transition: all cubic-bezier(.5,0,0,.5) 300ms;
}

.cover{
    position: absolute;
    top: 0;
    right: 0%;
    width: 0%;
    height: 100%;
    background-color: white;
    transition: all cubic-bezier(.5,0,0,.5) 300ms;
    pointer-events: none;
}
.cover{
    border-radius: 3px;
}
.cover.active{
    width: 100%;
}